import React from 'react';
import firebase from './Firebase';
import {
  CButton,
  CCard,
  CCardBody,
  CCardGroup,
  CFormGroup,
  CLabel,
  CFormText,
  CCol,
  CContainer,
  CCardHeader,
  CForm,
  CInput,
  CImg,
  CSwitch,
  CInputGroup,
  CInputGroupPrepend,
  CInputGroupText,
  CRow
} from '@coreui/react';
import CIcon from '@coreui/icons-react';
import './scss/style.scss';
import { icons } from './assets/icons/index.js';
import { withRouter } from 'react-router-dom';
React.icons = icons
class Jos2 extends React.Component{
  constructor(props) {
  super(props);
  this.state = {status: "",color: "",action:"",colorAction:"",imgurl:""}
  } 
	nyalakan = () => {
  if (this.state.status == "ON") {    
	firebase.database().ref('LED1').set("0");
    } else {
  firebase.database().ref('LED1').set("1");
    }  
  }
  getData = () => {
  firebase.database().ref('LED1').on('value', snapshot => {
    const state = snapshot.val();
    if (state == "1") {
      this.setState({status:"ON", color:"success", action:"Matikan", colorAction:"danger", onClick:"{this.matikan.bind(this)}", imgurl:"https://firebasestorage.googleapis.com/v0/b/controlmyac.appspot.com/o/led-on.png?alt=media&token=b6c3e2c2-7347-453e-89dc-81020f8df041"})
    } else {
      this.setState({status:"OFF", color:"danger", action:"Nyalakan", colorAction:"success", onClick:"{this.matikan.bind(this)}", imgurl:"https://firebasestorage.googleapis.com/v0/b/controlmyac.appspot.com/o/led-off.png?alt=media&token=fb22e3b2-2d8b-498c-935b-89c8df2d918a"})
    }
  })
}
  componentDidMount = () =>{
  this.getData();
}
	render(){
		console.log('darisini' , firebase)
		return(
<div className="c-app c-default-layout flex-row align-items-center">
      <CContainer>
        <CRow className="justify-content-center">
          <CCol md="6">
              <CCard className="p-4">
              <CCardHeader>
              <div class="d-flex justify-content-center">
                <CImg
                src={this.state.imgurl}
                fluid
                className="mb-2"
                width="10%"
                height="5%"
                />
              </div>
              </CCardHeader>              
                <CCardBody>
                <CRow>
        <CCol sm="12">
          <CForm action="" method="post">
            <CFormGroup>
            <CRow>
            <CCol md="12" xs="12">
            <div class="d-flex justify-content-start">
            <h3>Status :&nbsp;&nbsp;&nbsp;</h3><CButton disabled color={this.state.color}>{this.state.status}</CButton>
            </div>
            </CCol>
            </CRow>
            </CFormGroup>
            <CFormGroup>
              <CRow>
              <CCol md="12" xs="12">
              <div class="d-flex justify-content-start">
              <h3>Action :&nbsp;&nbsp;&nbsp;</h3><CButton onClick={this.nyalakan.bind(this)} color={this.state.colorAction}>{this.state.action}</CButton>
              </div>
              </CCol>
              </CRow>            
            </CFormGroup>
          </CForm>
        </CCol>
      </CRow>
                </CCardBody>
              </CCard>
          </CCol>
        </CRow>
      </CContainer>
    </div>			
///<button onClick={this.changevalue}>Changethis</button>
			)
	}
}
export default withRouter(Jos2);